.page-id-3623.milk {
    .banner img {
        width: 100%;
        margin: 0;
    }
    #Content {
        padding-top: 0px;
        padding-bottom: 45px;
        img {
            max-width: 100%;
            height: auto;
        }
        .content_wrapper {
            margin: 0 55px 0 40px;
            .border-left {
                position: fixed;
                height: 100vh;
                width: 60px;
                left: 0px;
                top: 50px;
                background-color: white;
                margin: 0;
                padding: 0;
            }
            .border-right {
                position: fixed;
                height: 100vh;
                width: 55px;
                right: -15px;
                top: 50px;
                background-color: white;
            }
            .border-bottom {
                position: fixed;
                width: 100vw;
                height: 55px;
                right: -15px;
                bottom: -40px;
                background-color: white;
                display: none;
            }
        }
    }
    .hero-section {
        overflow: hidden;
        .section_wrapper,
        .container {
            max-width: 100%;
        }
        .white-column {
            background-color: white;
            transition: all ease 1s;
            &.animated {
                width: 0%;
            }
        }
        .yellow-column {
            background-color: $yellow;
            transition: all ease 1s;
            
            &.animated {
                width: 100%;
            }
        }
        .white-column,
        .yellow-column {
            width: 50%;
            padding: 0;
            margin: 0;
            height: 1700px;
        }
        .container-logo {
            position: fixed;
            top: 50%;
            transform: translateY(-50%);
            transition: all ease 1s;
            opacity: 1;
            width: 90%;
            .mcb-wrap-inner {
                display: flex;
            }
            &.animated {
                transform: translate(-38.5%, -50%);
                position: fixed;
            }
            &.hidden {
                opacity: 0;
            }
            .image_wrapper {
                text-align: right;
                img {
                    width: 50%;
                }
            }
            .text-logo .image_wrapper {
                text-align: left;
            }
            .text-logo {
                opacity: 1;
                transition: all ease 500ms;
                &.hidden {
                    opacity: 0;
                }
            }
        }
        .hero-text {
            position: fixed;
            z-index: 0;
            transform: translate(15%, -50%);
            top: 48%;
            max-width: 75%;
            min-height: 160px;
            display: none;
            &.animated {
                display: block;
            }
            h3 {
                opacity: 0;
                position: absolute;
                transition: all ease 800ms;
                font-size: 60px;
                line-height: 80px;
                font-family: "poppins";
                z-index: 0;
                &.animated {
                    &#one {
                        opacity: 1;
                        z-index: 1;
                    }
                    &#two {
                        opacity: 1;
                        z-index: 2;
                    }
                    &#three {
                        opacity: 1;
                        z-index: 3;
                    }
                }
            }
        }
        .scroll-indicator {
            position: fixed;
            bottom: 55px;
            opacity: 1;
            width: auto;
            transition: all ease 400ms;
            width: 100%;
            margin: 0 0 35px -45px;
            .image_wrapper {
                text-align: center;
            }
            &.animated {
                opacity: 0;
                display: none;
            }
        }
        .container-logo.display-none {
            opacity: 0;
            z-index: 0;
            display: none;
        }
        .container-logo.opacity{
            opacity: 0;
        }
    }
    .meet-milkers-section {
        .banner {
            width: 100%;
            margin: 0;
        }
        padding: 0;
        margin: 0em 0;
        .column {
            width: 100%;
            margin: 0;
        }
        .meet-milkers {
            width: 50%;
            padding: 0;
            margin: 0;
            .meet-milkers-text {
                position: absolute;
                z-index: 1;
                top: 50%;
                transform: translate(-50%, -50%);
                width: 72%;
                left: 50%;
                text-align: left;
                font-size: 24px;
                line-height: 35px;
                font-family: "lato";
                h3 {
                    font-size: 33px;
                    line-height: 25px;
                    font-weight: 300;
                    letter-spacing: 0px;
                    font-family: "Poppins";
                }
                p {
                    color: black;
                }
            }
        }
        .bio-text {
            position: absolute;
            color: #FFF952;
            font-size: 16px;
            line-height: 23px;
            font-family: "lato";
            h4 {
                color: $yellow;
                position: relative;
                font-family: "Poppins";
                &:before {
                    display: inline-block;
                    content: "";
                    border-top: .4rem solid $yellow;
                    width: 4rem;
                    position: absolute;
                    top: 115%;
                }
            }
            &.adriana {
                width: 155px;
                top: 20%;
                padding-left: 25px;
                h4:before {
                    left: -31px;
                }
            }
            &.andres {
                right: 0;
                width: 155px;
                text-align: end;
                top: 8%;
                padding-right: 25px;
                h4:before {
                    right: -32px;
                }
            }
        }
    }
    .instagram-section {
        .banner {
            width: 100%;
            margin: 0;
        }
        margin: 2em 0;
        .column {
            width: 100%;
        }
        .instagram-feed-container {
            max-width: 80%;
            margin: 0 auto;
            float: none;
            h3 {
                font-size: 38px;
                margin: 1em 0;
                margin-top: 1em;
                font-weight: 600;
                margin-top: 1.2em;
                margin-bottom: 1.5em;
                &:before {
                    background-image: url(/wp-content/uploads/2018/07/Instagram.svg);
                    padding: 0px 45px;
                    content: "";
                    background-repeat: no-repeat;
                    top: 130%;
                    max-height: 50px;
                }
            }
        }
        .lines {
            position: absolute;
            top: 35%;
            left: 7%;
            width: auto;
        }
        .zig-zag {
            position: absolute;
            top: 15%;
            right: -2%;
            z-index: 10;
            width: auto;
        }
    }
    .contact-section {
        margin: 2em 0;
        .banner {
            width: 100%;
            margin: 0;
        }
        .column {
            width: 100%;
        }
        .contact-container {
            max-width: 64%;
            margin: 0 auto;
            float: none;
            h3 {
                font-size: 38px;
                margin: 1em 0;
                margin-top: 1em;
                font-weight: 600;
                margin-top: .8em;
                margin-bottom: 1.5em;
                &:before {
                    background-image: url(/wp-content/uploads/2018/07/Instagram.svg);
                    padding: 0px 45px;
                    content: "";
                    background-repeat: no-repeat;
                    top: 130%;
                    max-height: 50px;
                }
            }
            form {
                input,
                textarea {
                    border-color: #E5E5E5;
                    font-size: 12px;
                    font-family: "lato";
                    color: #707070;
                }
            }
            .wpcf7-submit {
                width: 100%;
                background-color: black;
                border: 1px solid black;
                border-radius: 0;
                transition: all ease 200ms;
                color: white;
                &:hover {
                    border: 1px solid black;
                    background-color: white;
                    color: black;
                }
            }
        }
    }
    .blog-section {
        .banner {
            width: 100%;
            margin: 0;
        }
        .blog-title h3 {
            font-size: 55px;
            margin: 1em 0;
            margin-top: 1em;
            margin-bottom: 1em;
            font-weight: 600;
            margin-top: .8em;
            margin-bottom: 1.5em;
            position: absolute;
            top: 37%;
            left: 6%;
        }
        .milk-blog-container {
            padding-top: 4em;
            .milk-blog {
                .post-item {
                    .image_frame .image_wrapper .image_links a {
                        background-color: $yellow;
                        i {
                            color: black;
                        }
                        &:hover {
                            background: $yellow;
                            color: #0095eb;
                            i {
                                color: black;
                                transform: scale(1.2);
                            }
                        }
                    }
                    .post-desc-wrapper {
                        .post-desc {
                            padding: 0px 20px 20px 10px;
                            .entry-title {
                                font-weight: 600;
                                letter-spacing: 0px;
                                font-family: "Lato";
                                font-size: 20px;
                            }
                            .post-excerpt {
                                margin-bottom: 15px;
                                font-size: 12px;
                                line-height: 17px;
                                font-family: "lato";
                            }
                        }
                        .post-footer {
                            background-color: $yellow;
                            padding: 3px;
                            .button-love,
                            .icon-comment-empty-fa,
                            .post-comments,
                            .icon-doc-text {
                                display: none;
                            }
                            .post-links{
                                border: 0;
                            }
                            .post-more {
                                color: black;
                                margin-right: 15px;
                                transition: all ease 300ms;
                                &:hover {
                                    font-weight: 600;
                                    text-decoration: none;
                                }
                            }
                        }
                    }
                }
                .button {
                    background-color: #E8E8E8;
                    color: black;
                    border: 1px solid #E8E8E8;
                    border-radius: 0;
                    width: 95%;
                    transition: all ease 300ms;
                    &:hover {
                        color: black;
                        background-color: rgba(232, 232, 232, 0.842);
                        border: 1px solid #E8E8E8;
                    }
                    .button_icon {
                        display: none;
                    }
                    .button_label {
                        float: none;
                        position: relative;
                        z-index: 2;
                        font-family: "lato";
                        font-size: 16px;
                    }
                }
            }
        }
    }
    .services-section {
        min-height: 850px;
        position: relative;
        h1 {
            font-size: 40px;
            font-weight: 600;
            margin: 25px 0;
        }
        .section_wrapper {
            padding: 2% 2% 2% 2%;
            position: absolute;
            top: 50%;
            transform: translate(-50%, -50%);
            left: 50%;
            width: 80%;
            background-color: white;
            .service-item {
                height: 200px;
                width: 200px;
                border: 1px solid gainsboro;
                transition: all ease 300ms;
                overflow: hidden;
                .service-wrapper {
                    display: flex;
                    overflow: hidden;
                    .image-wrapper {
                        width: 200px;
                        height: 200px;
                        position: relative;
                        margin: -1px;
                        cursor: pointer;
                        .image {
                            width: 98.4%;
                            height: 97.9%;
                            margin: 3px 1px 1px 3px;
                            &:before {
                                font-family: "milk-icons";
                                position: absolute;
                                font-size: 70px;
                                color: yellow;
                                left: 50%;
                                top: 50%;
                                transform: translate(-50%, -50%);
                            }
                        }
                        .title {
                            opacity: 1;
                            background: white;
                            position: absolute;
                            top: 0;
                            left: 0;
                            width: 98.4%;
                            height: 97.9%;
                            transition: all ease 300ms;
                            margin: 3px 1px 1px 3px;
                            font-weight: 600;
                            h5 {
                                position: absolute;
                                top: 50%;
                                left: 50%;
                                transform: translate(-50%, -50%);
                            }
                        }
                    }
                    .desc-wrapper {
                        width: 0px;
                        padding: 0px;
                        opacity: 0;
                        transition: all ease 300ms;
                        overflow: auto;
                        
                        .title,
                        .content {
                            width: 100%;
                            color: black;
                            font-size: 14px;
                            font-family: "Poppins";
                        }
                    }
                }
                &.open {
                    width: 475px;
                    .image-wrapper {
                        .title {
                            opacity: 0;
                        }
                    }
                    .desc-wrapper {
                        width: 380px;
                        padding: 15px;
                        opacity: 1;
                    }
                }
                &.brand-development {
                    .image-wrapper {
                        .image {
                            background-color: #675DC6;
                            &:before {
                                content: $icon-milk;
                            }
                        }
                    }
                }
                &.social-media {
                    .image-wrapper {
                        .image {
                            background-color: #43BEAC;
                            &:before {
                                content: $icon-coofee;
                            }
                        }
                    }
                }
                &.content-production {
                    .image-wrapper {
                        .image {
                            background-color: #FF9AC9;
                            &:before {
                                content: $icon-biberon;
                            }
                        }
                    }
                }
                &.digital-data {
                    .image-wrapper {
                        .image {
                            background-color: #6590E7;
                            &:before {
                                content: $icon-cookie;
                            }
                        }
                    }
                }
                &.email-marketing {
                    .image-wrapper {
                        .image {
                            background-color: #FC9A61;
                            &:before {
                                content: $icon-milk-shake;
                            }
                        }
                    }
                }
                &.influencer-marketing {
                    .image-wrapper {
                        .image {
                            background-color: #675DC6;
                            &:before {
                                content: $icon-cereal;
                            }
                        }
                    }
                }
                &.media-buying {
                    .image-wrapper {
                        .image {
                            background-color: #42BDAB;
                            &:before {
                                content: $icon-milk;
                            }
                        }
                    }
                }
            }
        }
        &:before {
            content: ""; // ::before and ::after both require content
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: transparent;
            opacity: .9;
            transition: all ease 400ms;
        }
        &.purple {
            &:before {
                background-color: #675DC6;
                transition: all ease 400ms;
            }
            .section_wrapper.mcb-section-inner {
                &:before {
                    content: url(../images/lines.png);
                    position: absolute;
                    left: 0;
                    top: -91px;
                    z-index: -95;
                }
            }
        }
        &.green {
            &:before {
                background-color: #42BDAB;
                transition: all ease 400ms;
            }
        }
        &.pink {
            &:before {
                background-color: #FF9AC9;
                transition: all ease 400ms;
            }
        }
        &.blue {
            &:before {
                background-color: #6590E7;
                transition: all ease 400ms;
            }
        }
        &.orange {
            &:before {
                background-color: #FC9A61;
                transition: all ease 400ms;
            }
        }
    }
}