.page-id-3623.milk {
    #back_to_top {
        display: none;
    }
    .section_wrapper,
    .container,
    .four.columns .widget-area {
        max-width: 100% !important;
        margin: 0 !important;
    }
    #Content {
        padding-top: 0px;
        .content_wrapper {
            margin: 0 0px 0 0px;
            .border-left {
                position: fixed;
                height: 100vh;
                width: 55px;
                left: 0px;
                top: 50px;
                background-color: white;
                display: none;
            }
            .border-right {
                position: fixed;
                height: 100vh;
                width: 55px;
                right: -15px;
                top: 50px;
                background-color: white;
                display: none;
            }
        }
    }
    .hero-section {
        overflow: hidden;
        .section_wrapper,
        .container {
            max-width: 100%;
            padding: 0 !important;
        }
        .white-column {
            background-color: white;
            transition: all ease 1s;
            &.animated {
                width: 0% !important;
            }
        }
        .yellow-column {
            background-color: yellow;
            transition: all ease 1s;
            &.animated {
                width: 100% !important;
            }
        }
        .white-column,
        .yellow-column {
            width: 50% !important;
            padding: 0;
            margin: 0;
            height: 1700px !important;
            clear: inherit;
        }
        .container-logo {
            position: fixed;
            top: 50%;
            transform: translateY(-50%);
            transition: all ease 1s;
            opacity: 1;
            width: 90%;
            &.animated {
                transform: translate(-38.5%, -50%);
                position: fixed;
            }
            &.hidden {
                opacity: 0;
            }
            .image_wrapper {
                text-align: right;
                img {
                    width: 60%;
                }
            }
            .text-logo .image_wrapper {
                text-align: left;
            }
            .text-logo {
                opacity: 1;
                transition: all ease 500ms;
                &.hidden {
                    opacity: 0;
                }
            }
        }
        .hero-text {
            position: fixed;
            z-index: 1000;
            transform: translate(20%, -50%) !important;
            top: 50%;
            h3 {
                font-size: 36px;
                line-height: 54px;
                word-wrap: break-word;
                left: 12%;
                vertical-align: middle;
                &.animated {
                    &#one {
                        opacity: 1;
                    }
                    &#two {
                        opacity: 1;
                    }
                    &#three {
                        opacity: 1;
                    }
                }
            }
        }
        .container-logo.display-none {
            opacity: 0;
        }
    }
    .meet-milkers-section {
        .section_wrapper,
        .container {
            max-width: 100%;
            padding: 0 !important;
            padding-left: 0px !important;
            padding-right: 0px !important;
        }
        padding: 0;
        margin: 0em 0;
        .column {
            width: 100%;
            margin: 0;
        }
        .meet-milkers {
            width: 100%;
            padding: 0;
            margin: 0;
            .meet-milkers-text {
                position: absolute;
                z-index: 1;
                top: 50%;
                transform: translate(-50%, -50%);
                left: 50%;
                font-size: 16px;
                line-height: 25px;
                width: 70% !important;
            }
            img {
                transform: scale(1.1)!important;
            }
        }
        .meet-milkers-photo {
            width: 100%;
        }
        .bio-text {
            position: absolute;
            color: yellow;
            font-size: 16px;
            line-height: 23px;
            h4 {
                color: yellow;
            }
            &.adriana {
                width: 35% !important;
                text-align: start;
                top: 3%;
                h4:before {
                    left: -31px;
                }
            }
            &.andres {
                width: 35% !important;
                text-align: end;
                top: 3%;
                h4:before {
                    right: -32px;
                }
            }
        }
    }
    .instagram-section {
        .section_wrapper,
        .container {
            max-width: 100%;
            padding: 0 !important;
        }
        margin: 2em 0;
        .column {
            width: 100%;
        }
        .column_image {
            margin: 0;
        }
        .instagram-feed-container {
            max-width: 90%;
            margin: 0 auto;
            float: none;
            h3 {
                font-size: 35px;
                margin: 1em 0;
                margin-top: 1em;
                font-weight: 600;
                margin-top: 1.2em;
            }
            #sb_instagram.sbi_col_4 #sbi_images .sbi_item {
                width: 100%;
            }
        }
        .lines {
            position: absolute;
            top: 35%;
            left: 7%;
            width: 13%;
        }
        .zig-zag {
            position: absolute;
            top: 15%;
            right: -2%;
            z-index: 10;
            width: 20%;
        }
    }
    .contact-section {
        margin: 2em 0;
        .section_wrapper,
        .container {
            max-width: 100%;
            padding: 0 !important;
        }
        .column {
            width: 100%;
        }
        .contact-container {
            max-width: 64%;
            margin: 0 auto;
            float: none;
            h3 {
                font-size: 38px;
                margin: 1em 0;
                margin-top: 1em;
                font-weight: 600;
                margin-top: .8em;
                margin-bottom: 1.5em;
                &:before {
                    background-image: url(/wp-content/uploads/2018/07/Instagram.svg);
                    padding: 0px 45px;
                    content: "";
                    background-repeat: no-repeat;
                    top: 130%;
                    max-height: 50px;
                }
            }
            form {
                input,
                textarea {
                    border-color: #E5E5E5;
                    font-size: 12px;
                    font-family: "lato";
                    color: #707070;
                }
            }
            .wpcf7-submit {
                width: 100%;
                background-color: black;
                border: 1px solid black;
                border-radius: 0;
                transition: all ease 200ms;
                color: white;
                &:hover {
                    border: 1px solid black;
                    background-color: white;
                    color: black;
                }
            }
        }
    }
}