@font-face {
    font-family: '#{$icomoon-font-family}';
    src: url('#{$icomoon-font-path}/#{$icomoon-font-family}.eot?736rhi');
    src: url('#{$icomoon-font-path}/#{$icomoon-font-family}.eot?736rhi#iefix') format('embedded-opentype'),
    url('#{$icomoon-font-path}/#{$icomoon-font-family}.ttf?736rhi') format('truetype'),
    url('#{$icomoon-font-path}/#{$icomoon-font-family}.woff?736rhi') format('woff'),
    url('#{$icomoon-font-path}/#{$icomoon-font-family}.svg?736rhi##{$icomoon-font-family}') format('svg');
    font-weight: normal;
    font-style: normal;
}

[class^="icon-"],
[class*=" icon-"] {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: '#{$icomoon-font-family}' !important;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.icon-milk_shake_icon {
    &:before {
        content: $icon-milk-shake;
        color: #fff952;
    }
}

.icon-milk_icon {
    &:before {
        content: $icon-milk;
        color: #fff952;
    }
}

.icon-coofee_icon {
    &:before {
        content: $icon-coofee;
        color: #fff952;
    }
}

.icon-cookie_icon {
    &:before {
        content: $icon-cookie;
        color: #fff952;
    }
}

.icon-biberon_icon {
    &:before {
        content: $icon-biberon;
        color: #fff952;
    }
}

.icon-cereal_icon {
    &:before {
        content: $icon-cereal;
        color: #fff952;
    }
}

.icon-facebook {
    &:before {
        content: $icon-facebook;
    }
}

.icon-twitter {
    &:before {
