    }
}

.icon-menu {
    &:before {
        content: $icon-menu;
    }
}

.milk {
    background-color: $grey-background;
    .button {
        background-color: black;
        color: white;
        border: 1px solid black;
        border-radius: 0;
        transition: all ease 300ms;
        padding: 15px;
        &:hover {
            color: black;
            background-color: white;
            border: 1px solid black;
        }
        .button_icon {
            display: none;
        }
        .button_label {
            float: none;
            position: relative;
            z-index: 2;
            font-family: "lato";
            font-size: 14px;
            padding: 0 15px;
        }
    }
    .fixed-nav .arrow {
        background-color: #fff952;
        color: black;
    }
    .post-item,
    .post {
        .image_frame .image_wrapper .image_links a {
            background-color: $yellow;
            i {
                color: black;
            }
            &:hover {
                background: $yellow;
                color: #0095eb;
                i {
                    color: black;
                    transform: scale(1.2);
                }
            }
        }
        .post-desc-wrapper {
            .post-desc {
                padding: 0px 20px 20px 10px;
                .entry-title {
                    font-weight: 600;
                    letter-spacing: 0px;
                    font-family: "Lato";
                    font-size: 20px;
                }
                .post-excerpt {
                    margin-bottom: 15px;
                    font-size: 12px;
                    line-height: 17px;
                    font-family: "lato";
                }
            }
            .post-footer {
                background-color: $yellow;
                padding: 3px;
                .button-love,
                .icon-comment-empty-fa,
                .post-comments,
                .icon-doc-text {
                    display: none;
                }
                .post-more {
                    color: black;
                    margin-right: 15px;
                    transition: all ease 300ms;
                    &:hover {
                        font-weight: 600;
                        text-decoration: none;
                    }
                }
            }
        }
    }
    .menu_wrapper .responsive-menu-toggle {
        i {
            display: none;
        }
        &:before {
            content: $icon-menu;
            font-family: 'milk-icons';
            font-size: 21px;
            line-height: 30px;
            color: black;
        }
        &:hover {
            text-decoration: none;
        }
    }
    #Top_bar.is-sticky .menu>li>a span {
        line-height: 53px;
    }
    .menu-main {
        li a {
            line-height: 53px !important;
        }
        #menu-item-3635 a {
            padding: 0px 20px 0px 20px !important;
            >span {
                display: none !important;
            }
            &:before {
                content: $icon-twitter;
                font-family: 'milk-icons';
                font-size: 25px;
            }
        }
        #menu-item-3636 a {
            padding: 0px 20px 0px 20px !important;
            >span {
                display: none !important;
            }
            &:before {
                content: $icon-instagram;
                font-family: 'milk-icons';
                font-size: 25px;
            }
        }
        #menu-item-3634 a {
            padding: 0px 20px 0px 20px !important;
            >span {
                display: none !important;
            }
            &:before {
                content: $icon-facebook;
                font-family: 'milk-icons';
                font-size: 25px;
            }
        }
    }
    #Header {
        margin: 0 55px;
        .logo {
            height: 55px;
            a {
                padding: 0 !important;
                font-size: 25px;
                height: 55px !important;
                line-height: 55px !important;
                font-weight: 600 !important;
            }
        }
        #Top_bar {
            .menu_wrapper a,
            .menu_wrapper span {
                font-family: 'Lato';
                font-family: 16px;
                font-weight: 600;
            }
            height: 55px;
            .menu.menu-main {
                li>a {
                    padding: 0;
                    height: 55px;
                    span {
                        height: 55px;
                        line-height: 55px;
                    }
                }
            }
            &.is-sticky {
                box-shadow: none;
                margin: 0 55px 0 0;
                .menu_wrapper {
                    margin-right: 80px;
                }
                #logo {
                    padding: 5px 0 !important;
                    height: 55px !important;
                    line-height: 45px !important;
                }
            }
        }
    }
    #Footer {
        z-index: 36;
       // position: fixed;
        bottom: 0;
        width: 100vw;
        background-color: white;
        .column.one {
            margin: 0;
            padding: 0;
        }
        .copyright {
            position: absolute;
            top: 15%;
            transform: translateY(50%);
            margin-left: 40px;
        }
    }
}